.quran-card {
  background-color: rgb(255, 255, 193) !important;
  color: black !important;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 200px;

  .error {
    font-size: 1.5em;
  }

  .loading-container {
    margin: 2rem 0rem;
  }

  .reference {
    font-weight: bold;
    // font-size: 1.2;
  }

  div.more {
    align-self: flex-end;
    .more {
      background-color: rgb(255, 208, 0);
      padding: 0.5rem 1rem;
      border-radius: 1rem;
      font-weight: bold;
      transition-duration: 300ms;
      margin-top: 1rem;
      text-decoration: none;
      display: inline-block;
      color: black;

      span + span {
        margin-left: 1em;
      }

      &.clickable:hover {
        background-color: rgb(223, 182, 0);
        // color: white;
      }
    }
  }

  .verse {
    font-weight: lighter;
    font-size: 1.5em;
    margin-bottom: 0.25em;
  }
}
